@import "../../style.scss";

.comments {
    padding: 0px;

    @include themify($themes) {
        img {
            width: 40px;
            height: 40px;
            border-radius: 50%;
            object-fit: cover;
        }

        .write {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
            margin: 20px 0px;

            input {
                flex: 5;
                width: 100%;
                padding: 10px;
                border-radius: 10px;
                border: 1px solid themed("border");
                background-color: transparent;
                color: themed("textColor");
            }

            button {
                border: none;
                font-weight: bold;
                font-size: 16px;
                background-color: themed("logo");
                color: themed("bg");
                padding: 10px;
                cursor: pointer;
                border-radius: 10px;
            }

            button:hover {
                filter: brightness(90%)
            }

            button:active {
                filter: brightness(80%);
            }
        }

        .comment {
            display: flex;
            flex-direction: row;
            border-radius: 5px;
            padding: 10px;

            .user-image {
                flex: 1;
            }

            .comment-container {
                flex: 9;
                display: flex;
                flex-direction: column;
                gap: 10px;

                .comment-info {
                    display: flex;
                    justify-content: space-between;

                    .user-name {
                        font-size: 18px;
                        font-weight: bold;
                    }

                    .date {
                        font-size: 12px;
                    }
                }

                .comment-content {
                    max-width: 500px;
                    overflow-wrap: break-word;
                }
            }



        }

        .comment:hover {
            background-color: themed("hovercomment");
        }
    }
}