@import "../../style.scss";

.post {
    @include themify($themes) {
        -webkit-box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.09);
        -moz-box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.09);
        box-shadow: 0px 0px 15px 1px rgba(0, 0, 0, 0.09);
        border-radius: 20px;
        background-color: themed('bg');
        color: themed("textColor");
        margin-top: 20px;
        gap: 0;
        .container {
            padding: 20px;
            .user {
                display: flex;
                align-items: center;
                justify-content: space-between;

                .userInfo {
                    display: flex;
                    gap: 20px;

                    img {
                        width: 40px;
                        height: 40px;
                        border-radius: 50%;
                        object-fit: cover;
                    }

                    .details {
                        display: flex;
                        flex-direction: column;

                        .name {
                            font-weight: 500;
                        }

                        .date {
                            font-size: 12px;
                        }

                    }
                }

                .extra-functions {
                    display: flex;

                    .dropdown {
                        .post-profile-dropdown {
                            margin-top: 30px;
                            margin-left: -130px;
                            padding: 5px;
                            position: absolute;
                            z-index: 90;

                            background-color: themed("bg");
                            border-style: solid;
                            border-color: themed("bgSoft");
                            border-width: 2px;
                            border-radius: 10px;

                            .list-item {
                                list-style-type: none;
                                display: flex;
                                flex-direction: column;


                                button {
                                    display: flex;
                                    justify-content: space-between;
                                    background-color: transparent;
                                    font-weight: bold;
                                    font-size: 12px;
                                    padding: 10px 15px 10px 15px;
                                    border: none;
                                    width: 100%;
                                    text-align: left;
                                    align-items: center;
                                    border-radius: 12px;
                                    color: themed("textColor");

                                    &:hover,
                                    &:focus {
                                        cursor: pointer;
                                        background-color: themed("hovercomment");
                                    }
                                }
                            }
                        }
                    }
                }
            }

            .content {
                margin: 20px 0px;

                .edit-container {
                    display: flex;
                    flex-direction: column;
                    gap: 10px;

                    .edit-text {
                        padding: 10px 15px;
                        font-family: 'Josefin Sans', sans-serif;
                        font-size: 16px;
                        border-radius: 8px;
                    }

                    .edit-button {
                        text-align: right;

                        button {
                            border-radius: 12px;
                            margin: 4px;
                            border: none;
                            padding: 8px 10px;
                            font-size: 16px;
                            color: white;
                            cursor: pointer;
                            background-color: themed("logo");
                            color: themed("bg");
                        }

                        button:hover {
                            filter: brightness(90%)
                        }

                        button:active {
                            filter: brightness(80%)
                        }

                    }

                }

                .slider {
                    position: relative;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    .left-arrow {
                        position: absolute;
                        top: 50%;
                        left: 32px;
                        font-size: 3rem;
                        color: #000;
                        z-index: 10;
                        cursor: pointer;
                        user-select: none;
                    }

                    .right-arrow {
                        position: absolute;
                        top: 50%;
                        right: 32px;
                        font-size: 3rem;
                        color: #000;
                        z-index: 10;
                        cursor: pointer;
                        user-select: none;
                    }

                    img {
                        width: 100%;
                        max-height: 500px;
                        object-fit: cover;
                        margin-top: 5px;
                    }

                    .slide {
                        opacity: 0;
                        transition-duration: 1s ease;
                    }

                    .slide.active {
                        opacity: 1;
                        transition-duration: 1s;
                        transform: scale(1);
                    }

                }

            }

            .info {
                display: flex;
                align-items: center;
                gap: 20px;

                .tool-tip {
                    z-index: 10;
                    margin-top: 3px;
                    margin-left: -5px;
                    position: absolute;
                    font-size: 12px;
                    padding: 6px 4px;
                    background-color: themed("textColorSoft");
                    color: themed("bg");
                    border-radius: 4px;
                    opacity: 0;
                    transition: opacity 0.15s;
                }

                .item {
                    display: flex;
                    align-items: center;
                    gap: 10px;
                    cursor: pointer;
                    font-size: 14px;

                }

                .item:hover+.tool-tip {
                    opacity: 1;
                }

            }
        }
    }
    .test{
        margin: 0;
        background-color: red;
    }
}